@media (min-width: 768px){
  .forecast-summary-div {
    height: 6rem;
  }
    .daily-forecast-summary-div {
          height: 6rem;
        }
}

@media (min-width: 1024px){
  .forecast-summary-div {
    height: 4.2rem;
  }
    .daily-forecast-summary-div {
          height: 6rem;
        }
}

@media (min-width: 1200px){
.forecast-summary-div {
      height: 4.2rem;
    }
    .daily-forecast-summary-div {
          height: 4.2rem;
        }
}






