.loading {
    position: relative;
}


.loading .centered {
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    position: absolute;
}

.loading-enabled {
    background-color: rgba(108, 108, 108, 0.3);
    cursor: not-allowed;
}
